import { MDBCard, MDBCardBody, MDBContainer } from "mdb-react-ui-kit";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

function FilterSchools() {
  const navigate = useNavigate();
  const [sch, setSch] = useState("");

  function handleFindSchool() {
    navigate("/school", {
      state: {
        school_name: sch,
      },
    });
  }

  return (
    <div>
      <MDBContainer>
        <MDBCard>
          <h3> Filter Schools : </h3>
          <MDBCardBody>
            <h3> Find School</h3>
            <form>
              <input
                type="text"
                placeholder="Enter School Name"
                value={sch}
                onChange={(event) => setSch(event.target.value)}
              />
              <button type="button" onClick={handleFindSchool}>
                Find
              </button>
            </form>
          </MDBCardBody>
        </MDBCard>
      </MDBContainer>
    </div>
  );
}

export default FilterSchools;
